import React, {useState, useEffect} from 'react';
import {Typography, Button, Table, TableBody, TableCell, Box, TextField,
    TableContainer, TableHead, TableRow, Paper, Link, IconButton, RadioGroup, FormControlLabel, Radio} from '@material-ui/core';
import {Alert, Autocomplete} from '@material-ui/lab';
import {Close} from '@material-ui/icons';
import {useDropzone} from 'react-dropzone';
import ImageDialog from './ImageDialog';
import moment from 'moment';
import { getEnvironment } from '../../CommonFunctions';
import { saveFilterImages, loadFilterImage, deleteFilterImage, getArEffects, getArBinaries } from '../../ApiUtility/ApiUtility';

const styles = {
    container: {
        height: '50px'
    },
    label: {
        marginRight: '10px',
        display: "inline-block"
    },
    uploadButton: {
        fontSize: '10px',
        padding: '5px 5px'
    },
    closeButton: {
        padding: '0px'
    },
    section_container: {
        marginTop: '10px',
        marginBottom: '10px'
    }
}

export default function ARUpload(props) {


    const [ar1Url, setAr1Url] = useState("");
    const [arBeautUrl, setArBeautUrl] = useState("");
    const [imageDialogOpen, setImageDialogOpen] = useState(false);
    const [dialogType, setDialogType] = useState("");
    const [arEffects, setArEffects] = useState([]);


    //handle both sticker and filter types 
    const onDrop = (file, type) => {
        let nextAREffects = props.uploadedAREffects.slice(0);
        nextAREffects[type-1] = file[0];
        
        props.setUploadedAREffects(nextAREffects);
    };

    const {getRootProps: getRootPropsArEffect, getInputProps: getInputPropsArEffect} = useDropzone({onDrop: f => onDrop(f, "1")});
    const {getRootProps: getRootPropsArBeaut, getInputProps: getInputPropsArBeaut} = useDropzone({onDrop: f => onDrop(f, "arb")});

    const handleCloseDialog = e => {
        setImageDialogOpen(false);
        setDialogType("");
    }

    const handleClick = x => {
        setImageDialogOpen(true);
        setDialogType(x);
    }

    const handleDelete = type => {
        let filter_id = props.selectedFilter.frame_id;
        deleteFilterImage({type, filter_id}).then(r => {
            if(type == "ar1") setAr1Url("");
            else if(type == "arb") setArBeautUrl("");
        }).catch(err => err)
    }

    const handleARChange = (e,v) => {
        console.log("e, v: ", e, v);
        getArBinaries(v.id).then(r => console.log(r))
    }

    //change setfilterexists to hold the url if exists and set equal to false if doesn't 
    //change the api calls to load and cache image
    useEffect(() => {
        loadFilterImage({filter_id: props.selectedFilter.frame_id, type: "ar1"})
            .then(f => setAr1Url(f)).catch(err => err)
        loadFilterImage({filter_id: props.selectedFilter.frame_id, type: "arb"})
            .then(f => setArBeautUrl(f)).catch(err => err)
    }, []);

    const getImageUrl = e => {
        if(dialogType == "AR1"){
            return `${getEnvironment()}.cameraplus.co/CameraPlus/gc/sys/objblob/frame/${props.selectedFilter.frame_id}/ar1?dt=${moment().format()}`;
        }
    }

    useEffect(()=>{
        getArEffects().then(r => setArEffects(r.data))
    }, [])
    

  return (
    <>
    <ImageDialog 
        dialogOpen={imageDialogOpen}  
        dialogType={dialogType}
        handleCloseDialog={handleCloseDialog}
        img_src={getImageUrl()}
    />
    <TableContainer component={Paper}>
        <Alert className={!props.imageUploadErr ? "hidden" : ""} variant="outlined" severity="error">
            {props.imageUploadErr}
        </Alert>
    <Table aria-label="simple table">
        <TableHead>
            <TableRow>
            <TableCell>Asset</TableCell>
            {/* <TableCell align="right">Upload</TableCell> */}
            <TableCell align="right">Delete</TableCell>
            </TableRow>
        </TableHead>
        <TableBody>
            <TableRow key="AR1">
                {/* <TableCell component="th" scope="row">
                    <Typography variant="body2" style={styles.label} gutterBottom></Typography>
                </TableCell> */}
                <TableCell align="right">
                    <Autocomplete
                        id="ar-asset-select"
                        options={arEffects}
                        onChange={handleARChange}
                        getOptionLabel={(option) => option.name}
                        renderOption={(option) => <div style={{fontSize:"12px"}}>{option.name}</div>}
                        size="small"
                        style={{ fontSize: '12px' }}
                        renderInput={(params) => <TextField style={{fontSize: '12px'}} {...params} label="Face Filter" variant="outlined" />}
                    />
                    {/* <div {...getRootPropsArEffect({ refKey: 'innerRef' })}>
                        <input {...getInputPropsArEffect()}/>
                        <Button variant="contained" color="primary" style={styles.uploadButton}>
                            Upload Image 
                        </Button>
                    </div> */}
                </TableCell>
                <TableCell align="right">
                    <Link style={{cursor: "pointer"}} onClick={e => handleDelete("ar1")} 
                        className={!!ar1Url ? "" : "hidden"}
                    >
                        Delete
                    </Link>
                </TableCell>
            </TableRow>
            <TableRow key="ARB">
                <TableCell component="th" scope="row">
                    <Typography variant="body2" style={styles.label} gutterBottom></Typography>
                </TableCell>
                {/* <TableCell align="right">
                    <div {...getRootPropsArBeaut({ refKey: 'innerRef' })}>
                        <input {...getInputPropsArBeaut()}/>
                        <Button variant="contained" color="primary" style={styles.uploadButton}>
                            Upload Image 
                        </Button>
                    </div>
                </TableCell> */}
                <TableCell align="right">
                    <Link style={{cursor: "pointer"}} onClick={e => handleDelete("arb")} 
                        className={!!arBeautUrl ? "" : "hidden"}
                    >
                        Delete
                    </Link>
                </TableCell>
            </TableRow>

      </TableBody>
    </Table>
  </TableContainer>
  </>
  );
}
