import React, {useState, useEffect, useRef} from 'react';
import {getEnvironment} from '../../CommonFunctions';
import axios from 'axios';


export default function ARPreview(props) {
    
    const canvasRef = useRef();

    useEffect(()=>{

        // axios.get(
        //     `${getEnvironment()}.cameraplus.co/CameraPlus/gc/sys/objblob/frame/1593/ar1`, 
        //     {headers:{ContentRange: 100}})
        //     .then(e => 

        // axios.head(`${getEnvironment()}.cameraplus.co/CameraPlus/gc/sys/objblob/frame/${props.selectedFilter.frame_id}/ar1`)
        //     .then(e => 
        // return;
        if(!canvasRef.current || getEnvironment() == "https://prod" || props.campaign.name !== "AR") return;
        let deepAR = window.DeepAR({
            canvasWidth: props.canvasWidth || 500,
            canvasHeight: props.canvasHeight || 500,
            licenseKey: '79fbad87721515553bf1c64464a02eaa027d63baccb3874e83ef53ebc89ca2c95fb405d43ee12be2',
            canvas: canvasRef.current,
            numberOfFaces: 1,
            libPath: window.location.origin + '/portal/v2/cameraplus/deepar_lib',
            segmentationInfoZip: window.location.origin + '/portal/v2/cameraplus/deepar_lib/segmentation.zip',
            onInitialize: function() {
                
              // start video immediately after the initalization, mirror = true
              deepAR.startVideo(true);
              deepAR.switchEffect(0, 'slot', `${getEnvironment()}.cameraplus.co/CameraPlus/gc/sys/objblob/frame/${props.selectedFilter.frame_id}/ar1`, function() {
                // effect loaded
              });
            }
        })

        deepAR.downloadFaceTrackingModel(window.location.origin + '/portal/v2/cameraplus/deepar_lib/models-68-extreme.bin');
        
        return () => {
            deepAR.stopVideo();
        }
    
    }, [canvasRef]);

    return (
        <div style={{
                position: 'absolute',
                width: props.canvasWidth + "px",
                height: props.canvasHeight + "px",
                float: 'right',
                right: '25px'
            }}
        >
            <canvas 
                id="deepar-canvas" 
                ref={canvasRef} 
                style={{
                    width: props.canvasWidth + "px",
                    height: props.canvasHeight + "px"
                }} 
            />
        </div>
    );
}